@import url(https://fonts.googleapis.com/css2?family=Average+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Average Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;  
}

.appWrapper {
  position: relative;
  padding: 50px 20px 0 80px;
  max-width: 900px;
  width: 100%;
}


.customRow {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 700px;
  margin-top: 20px;
}

.sliderContainer{ 
   width: 30px;
   height: 420px;
  position: absolute; 
  left: 40px; 
  display: flex;
  align-items: center;
  flex-direction: column;

}

.sliderContainer span {
  text-align: center;
  font-size: 1em;
  font-weight: bold;
  color: #fff;
}
.sliderContainer .rc-slider-vertical .rc-slider-handle  {
  margin-left: -15px;
}
.sliderContainer .rc-slider-handle {
  width: 34px;
  height: 34px;
  border-color: #efefef;
}
.sliderContainer .rc-slider-vertical .rc-slider-rail {
  height: 100%;
  width: 8px;
  margin-left: -2px;
}
.donut-complex text {
	font-family: 'Average Sans', sans-serif;
		font-size: 0.35rem;
	font-weight: bold;
	opacity: 0;
	transition: opacity 0.35s ease-in-out 1s;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	color: #000;
	fill: #000;
	cursor: pointer;

}
.donut-complex text.whiteText {
	color: #fff;
	fill: #fff;
}

.donut-complex circle.segmentCircle {
	fill: none;
	cursor: pointer;
	/* transition: stroke-dashoffset 1s ease-in-out; */
}
.donut-complex .segmentGroup:hover circle.segmentCircle {
	stroke-width: 50px;
}
.donut-complex circle.centerCircle {
	fill: #000;
	/* transition: stroke-dashoffset 1s ease-in-out; */
}

.donut-complex:not(.donut-complex--loaded) circle {
	stroke-dashoffset: 0;
}

.donut-complex--loaded text {
	opacity: 1;
}
.OffButton,
.OffButtonInner,
.whiteText, 
.offButtonWrapper {
	cursor: pointer;
}
.CustomValue {
    background-color: #727272;
    padding: 20px 0;
    border-radius: 20px;
}
.CustomValue h3 {
    font-size: 18px;
    color: #fff;
    padding: 0 10px;
    margin: 0;
}
.CustomValue .fields { 
    display: flex;
    flex-direction: row;
}
.CustomValue .fields input {
    padding: 20px;
    font-size: 24px;
    max-width: 100px;
    border-radius: 10px;
    margin: 10px;
}
.CustomValue .setButton {
    margin-left: 10px;
    padding: 5px 20px;
    font-size: 28px;
    background-color: #9b9b9b;
    border-radius: 10px;
    border: 2px solid #000;
    float: left;
    cursor: pointer;
}
.CustomValue .setButton:hover {

    background-color: #efefef;
    }
.CustomValue.selected .setButton {

background-color: #fff;
}
